.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.list-group-item{
  color: black;
}

.fixed-list{
  max-height: 300px;
  overflow: scroll;
}

.show-xs-only {
  display: none;
}

@media (max-width: 767px) {
  .hidden-xs{
      display: none;
  }

  .show-xs-only{
    display: block;
  }
}


